    0% { background-color: orange; }
    100% { background-color: greenyellow; }
  }
  @keyframes pulse {
    0% { background-color: orange; }
    100% { background-color: greenyellow; }
  }

  .pulse3 {
    -webkit-animation: pulse4 500ms infinite alternate;
            animation: pulse4 500ms infinite alternate;
  }
  @-webkit-keyframes pulse4 {
    0% { color: orangered; }
    50% { color: yellow; }
    100% { color: green; }
  }
